.App {
  text-align: center;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font: normal 16px sans-serif;
  color: #555;
}

a {
  text-decoration: none;
  opacity: 0.75;
  color: white;
}

a:hover {
  opacity: 1;
}

.nav-bar  {
  position: absolute;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 35px 0 0 0;
  animation: 1s fadein 0.5s forwards;
  opacity: 0;
  z-index: 1;
}

@keyframes fadein {
  100% {
    opacity: 1;
  }
}

.nav-links {
  list-style: none;
  display: flex;
  /*flex-direction: row;
  justify-content: space-around;
  align-items: center;*/
  margin-right: -15px;
}

.nav-links li {
  margin: 0 15px;
}

header h2 {
  font-family: "Quicksand", sans-serif;
}

.hero .background-image {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-size: cover;
  z-index: -1;
  background-color: #80a3db;
  background: url('https://cdn.mos.cms.futurecdn.net/ntFmJUZ8tw3ULD3tkBaAtf.jpg') no-repeat center fixed;
}

.btn {
  border-radius: 4px;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  background-color: #3f51b5;
  opacity: 1;
  color: white;
  z-index: 1;
}

a.btn:hover {
  box-shadow: 0 0 20px 0 rgba(0,0,0,0.75);
  transition: box-shadow 2s 0s linear;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 100px 80px;
}

.hero {
  position: relative;
  justify-content: center;
  text-align: center;
  min-height: 100vh;
  color: white;
  background: url('https://cdn.mos.cms.futurecdn.net/ntFmJUZ8tw3ULD3tkBaAtf.jpg') no-repeat center fixed;
  background-size: cover;
}

div.hero-contentÂ {
  opacity: 0;
  margin-top: 100px;
  animation: 1s slidefade 1s forwards;
}

.hero h1 {
  font: bold 60px "Open sans", sans-serif;
  margin-bottom: 15px;
}

.hero h3 {
  font: bold 28px "Open sans", sans-serif;
  margin-bottom: 40px;
  padding: 0 10rem;
}

.hero a.btn {
  padding: 20px 46px;
}

@keyframes slidefade {
  100% {
    opacity: 1;
    margin: 0;
  }
}